import React, { useState, useRef, useEffect, useCallback } from "react";
// styles
import styles from "./index.module.scss";
import { useDispatch, useSelector } from "react-redux";
// internal components
import Popover from "react-bootstrap/Popover";
import OverlayTrigger from "react-bootstrap/OverlayTrigger";
import NormalMultiSelect from "components/UI/common/NormalMultiSelect";
import NormalButton from "components/UI/common/NormalButton/NormalButton";
import { sleep } from "Constants/helperFunctions";
import TabComponent from "components/UI/common/Tab/TabComponent";
import FirstField from "./TableForm";
import { getRoomTypesList, addRoomPackages } from "service/RoomPackages";
import ToolTip from "components/UI/common/Tooltip";
import { setTabDirty, setTabStatus } from "redux/slice/basicSlice";
import { getTabStatusDetails } from "service/TabDetails";
import { Toast } from "Constants/toast";
import { ReactComponent as InfoIcon } from "assets/images/infoIcon.svg";
import DeleteModal from "components/UI/common/DeleteModal";
import debounce from "lodash/debounce";

/* 
  Note: 1. shortstay is empty then that row is occupancy row
        2. sharing row added in occupancy sharingData property
*/

const RoomPackagesForm = ({
  handleNextTab,
  showPreview,
  activeTab: urlActiveTab,
  isPreviewScreen,
  childSaveDraftRef,
}) => {
  const formRef = useRef();
  const dispatch = useDispatch();

  const [activeTab, setActiveTab] = useState(0);
  const [roomTypes, setRoomTypes] = useState([]);
  const [activeTabName, setActiveTabName] = useState("");
  const propertyId = useSelector((state) => state.basic.propertyId);
  const [isSubmitLoading, setIsSubmitLoading] = useState(false);
  const [stayType, setStayType] = useState("ALL");
  //const [prevStayType, setPrevStayType] = useState("ALL");
  const [occupancy, setOccupancy] = useState("ALL");
  //const [prevOccupancy, setPrevOccupancy] = useState("ALL");
  const [saveDraft, setSaveDraft] = useState(false);
  const [packageDetails, setPackageDetails] = useState([]);
  const [deletedArray, setDeletedArray] = useState([]);
  const [showFilter, setShowFilter] = useState(false);
  const [isDirty, setIsDirty] = useState(false);
  const [showWarning, setShowWarning] = useState(null);
  const [showWarningOccupancy, setShowWarningOccupancy] = useState(null);
  const [tabStatus, setTabStatusValue] = useState("up next");
  const PropertyStatus = useSelector((state) => state.basic.propertyStatus);
  const TotalDraft = useSelector((state) => state.basic.TotalDraft);
  const UnitCreationDraft = useSelector(
    (state) => state.basic.unitCreationDraft
  );
  const PendingProperty = useSelector(
    (state) => state.basic.showPendingProperty
  );

  const occupancyDropdownData = [
    { value: "ALL", label: "All" },
    { value: "FULL_ROOM", label: "Full Room" },
    { value: "SHARING", label: "Sharing" },
  ];

  const stayTypeOptions = [
    { value: "ALL", label: "All" },
    { value: "LONG_STAY", label: "Long Stay" },
    { value: "SHORT_STAY", label: "Short Stay" },
  ];

  const getTabData = useCallback(async () => {
    try {
      const response = await getTabStatusDetails(propertyId);
      if (response.status === 200) {
        const filteredData = response.data?.find(
          (o) => o?.tabName?.toLowerCase() === "room packages"
        );
        setTabStatusValue(filteredData?.tabStatus?.toLowerCase());
      } else {
        Toast({ type: "error", message: response.data.message });
      }
    } catch (e) {
      console.log("e :>> ", e);
    }
  }, []);

  const checkNextTabsCompletion = async () => {
    try {
      const response = await getTabStatusDetails(propertyId);
      if (response?.data?.length) {
        return ["Service Amenities"].every(
          (tab) =>
            response?.data?.findIndex(
              (a) => a.tabName === tab && a?.tabStatus == "COMPLETED"
            ) >= 0
        );
      }
      return true;
    } catch (e) {
      console.log("e :>> ", e);
    }
  };

  useEffect(() => {
    dispatch(setTabDirty(isDirty));
  }, [isDirty]);
  useEffect(() => {
    setTimeout(() => {
      getTabData();
    }, 500);
  }, [getTabData]);

  const handleTabChange = (i) => {
    setActiveTab(i);
    setActiveTabName(roomTypes[i]?.title);
  };

  const handleSaveDraft = (e, isValidate) => {
    //setSaveDraft(true);
    setIsSubmitLoading(true);
    const form = formRef?.current?.watch();
    onsubmit(form, isValidate);
    //handleNextTab();
  };

  const getRoomTypes = async (page) => {
    const response = await getRoomTypesList(propertyId);
    if (response?.data?.data?.length) {
      const data = response.data.data;
      const transformData = data.map((roomType, i) => {
        const isShortStay = roomType.roomCategories.some((o) => o.shortStay);
        const isLongStay = roomType.roomCategories.some((o) => o.longStay);
        let tempObj = {
          ...roomType,
          index: i + 1,
          title: roomType.roomType,
          isFilled: false,
          data: [],
          isShortStay,
          isLongStay,
        };
        if (isShortStay) {
          let tempObj2 = {
            propertyId,
            roomType: roomType.roomType,
            roomTypeId: roomType._id,
            stayType: "SHORT_STAY",
            occupancy: "",
            sharing: "",
            baseAdults: "",
            baseChild: "",
            extraAdult: "",
            extraChild: "",
            contractType: "DAILY",
            baseRate: "",
            minThresholdRate: "",
            maxThresholdRate: "",
            extraAdultCost: "",
            extraChildCost: "",
            rentPaymentModel: "",
            securityDepositMonths: "NA",
            tokenAmountPercentage: "",
          };

          tempObj.data.push({
            key: "SHORT_STAY",
            data: [tempObj2],
          });
        }
        if (isLongStay) {
          tempObj.data.push({
            key: "LONG_STAY",
            data: [
              {
                propertyId,
                roomType: roomType.roomType,
                roomTypeId: roomType._id,
                stayType: "LONG_STAY",
                occupancy: "",
                sharing: "",
                baseAdults: "",
                baseChild: "",
                extraAdult: "",
                extraChild: "",
                contractType: "MONTHLY",
                baseRate: "",
                minThresholdRate: "",
                maxThresholdRate: "",
                extraAdultCost: "",
                extraChildCost: "",
                rentPaymentModel: "",
                securityDepositMonths: "",
                tokenAmountPercentage: "",
              },
            ],
          });
        }
        return tempObj;
      });
      setRoomTypes(transformData);
      setActiveTabName(transformData[0]?.title);
    }
  };

  useEffect(() => {
    if (!roomTypes.length) getRoomTypes();
  }, []);

  const onsubmit = async (data, isValidate) => {
    //setIsSubmitLoading(true)
    const isPost = packageDetails.length === 0;
    const roomPackages = data.fieldArray.reduce((acc, field) => {
      field.data.map((detail) => {
        detail.data = detail.data.map((o) => ({
          ...o,
          occupancy: o.occupancy === null ? "SHARING" : o.occupancy,
        }));
        acc = [...acc, ...detail.data];
      });
      return acc;
    }, []);
    const form = {
      propertyId,
      isDraft: isValidate === true,
      roomPackages: roomPackages,
      ...(!isPost
        ? {
            deletedArray,
          }
        : {}),
    };
    try {
      await addRoomPackages(form, isPost);
      dispatch(setTabDirty(false));
      dispatch(setTabStatus());
      if (isValidate !== true) {
        if (isPreviewScreen) {
          const isNextTabsCompleted = await checkNextTabsCompletion();
          console.log(
            "isNextTabsCompletedisNextTabsCompleted",
            isNextTabsCompleted
          );
          if (!isNextTabsCompleted) handleNextTab(null, false, false, true);
          else handleNextTab(urlActiveTab, false, isPreviewScreen, true);
        } else handleNextTab(null, false, false, true);
      }
      setTimeout(() => {
        setSaveDraft(false);
        getTabData();
      }, 500);
    } catch (e) {
      // handle your error
      console.log("errrrrrrrrrrr", e);
    } finally {
      setIsSubmitLoading(false);
    }
  };

  const handleOnSubmit = (e) => {
    setIsSubmitLoading(true);
    if (formRef.current) {
      const { handleOnSubmit } = formRef.current;
      handleOnSubmit(onsubmit, e);
    }
  };

  const handleDeletedArray = (_id) => {
    if (_id) {
      setDeletedArray((prev) => [...prev, { _id }]);
    }
  };

  const onSelect = async (e, newValue) => {
    if (isDirty == false) {
      setStayType(e.target.value);
    } else {
      //setSelectedFrequency(e.target.value);
      setShowWarning(e.target.value);
    }
  };

  const onSelectOccupancy = async (e, newValue) => {
    if (isDirty == false) {
      setOccupancy(e.target.value);
    } else {
      setShowWarningOccupancy(e.target.value);
    }
  };

  return (
    <div className={styles.roomPackagesForm}>
      <div className={`${styles.contentPart} px-3`}>
        <div className="new-section py-2">
          <div className={styles.basicTitle}>
            {showPreview ? (
              <h3 className={styles.standardBlack}>Room Packages</h3>
            ) : (
              <h3 className={styles.standardBlack}>Create Room Packages</h3>
            )}
            <div className="d-flex gap-3 ">
              <ToolTip label="Filters are only applicable for saved data.">
                <InfoIcon className="mt-2" />
              </ToolTip>
              <div className={styles.selectWrap}>
                <NormalMultiSelect
                  name="occupancy"
                  isSearchable={false}
                  options={occupancyDropdownData}
                  placeholder={"Occupancy: All"}
                  disabled={
                    String(tabStatus).toUpperCase() === "UP NEXT" ||
                    isDirty === true
                      ? true
                      : false
                  }
                  handleChange={onSelectOccupancy}
                  value={occupancy}
                  supportLabel={"Occupancy: "}
                />
              </div>
              <div className={`${styles.selectWrap}`}>
                {/* <NormalMultiSelect
                  name="stayType"
                  isSearchable={false}
                  options={stayTypeOptions}
                  placeholder={"Stay type: All"}
                  disabled={
                    String(tabStatus).toUpperCase() === "UP NEXT" ? true : false
                  }
                  handleChange={async (e, { value } = {}) => {
                    if (isDirty === false) {
                      setStayType(value);
                    }
                    else {
                      setShowWarning(value);
                    }
                  }}
                /> */}
                <NormalMultiSelect
                  name="stayType"
                  isSearchable={false}
                  options={stayTypeOptions}
                  placeholder={"Stay type: All"}
                  disabled={
                    String(tabStatus).toUpperCase() === "UP NEXT" ||
                    isDirty === true
                      ? true
                      : false
                  }
                  handleChange={onSelect}
                  value={stayType}
                  supportLabel={"Stay type: "}
                />
              </div>
            </div>
          </div>
        </div>
        <div className={styles.tabwrap}>
          <TabComponent
            tabs={roomTypes}
            setActiveTab={handleTabChange}
            isRoomPackage
            activeTab={activeTab}
          />
        </div>

        <div className={styles.tableWrap}>
          <div className={styles.tableborder}>
            {roomTypes.length > 0 && (
              <FirstField
                formRef={formRef}
                activeTabName={activeTabName?.toLowerCase() || ""}
                roomTypeId={roomTypes?.[activeTab]?._id}
                saveDraft={saveDraft}
                form={roomTypes}
                activeTab={activeTab}
                roomType={roomTypes?.[activeTab]}
                propertyId={propertyId}
                setActiveTab={setActiveTab}
                setPackageDetails={setPackageDetails}
                packageDetails={packageDetails}
                isSubmitLoading={isSubmitLoading}
                stayType={stayType}
                occupancy={occupancy}
                handleDeletedArray={handleDeletedArray}
                setIsDisabled={setIsSubmitLoading}
                setShowFilter={setShowFilter}
                // prevStayType={prevStayType}
                // prevOccupancy={prevOccupancy}
                setIsDirty={(value) => {
                  setIsDirty(value);
                }}
                showPreview={showPreview}
              />
            )}
          </div>
        </div>
      </div>
      {!PendingProperty && PropertyStatus == "PENDING" ? <></> : <hr />}
      {showPreview ? (
        !PendingProperty ? (
          <>
            {!TotalDraft && PropertyStatus !== "PENDING" && (
              <div className="d-flex justify-content-start">
                <div
                  className={`d-flex justify-content-start ${styles.btn} py-2 px-2 col`}
                >
                  <div className={styles.btnWidth}>
                    {PropertyStatus == "APPROVED" &&
                      UnitCreationDraft == false && (
                        <NormalButton
                          className={"draftBtn"}
                          label={"Add More Units"}
                          //id={"draftBtn"}
                          //onClick={(e) => handleSaveDraft(e)}
                          onClick={(e) => {}}
                        />
                      )}
                  </div>
                </div>
                <div
                  className={`d-flex justify-content-end ${styles.btn} py-2 px-2 col`}
                >
                  <div className={styles.btnWidth}>
                    {PropertyStatus !== "REJECTED" ? (
                      <NormalButton
                        className={"draftBtn"}
                        label={"Edit Details"}
                        //id={"draftBtn"}
                        //onClick={(e) => handleSaveDraft(e)}
                        onClick={(e) => {
                          handleNextTab(urlActiveTab, true);
                        }}
                        disabled={
                          ((stayType !== "ALL" || occupancy !== "ALL") &&
                            true) ||
                          isSubmitLoading
                        }
                      />
                    ) : (
                      <NormalButton
                        className={"submitBtn"}
                        label={"Edit Details"}
                        //id={"draftBtn"}
                        //onClick={(e) => handleSaveDraft(e)}
                        onClick={(e) => {
                          handleNextTab(urlActiveTab, true);
                        }}
                        disabled={
                          ((stayType !== "ALL" || occupancy !== "ALL") &&
                            true) ||
                          isSubmitLoading
                        }
                      />
                    )}
                  </div>
                  {(UnitCreationDraft == "" || UnitCreationDraft == null) &&
                  PropertyStatus !== "REJECTED" ? (
                    <div className={styles.btnWidth}>
                      <NormalButton
                        className={"submitBtn"}
                        label={"Add Units"}
                        id="AddunitBtn"
                        onClick={debounce(async (e) => {
                          //handleNextTab();
                        }, 800)}
                      />
                    </div>
                  ) : UnitCreationDraft == true &&
                    PropertyStatus !== "REJECTED" ? (
                    <div className={styles.btnWidth}>
                      <NormalButton
                        className={"submitBtn"}
                        label={"Continue Unit Creation"}
                        id="AddunitBtn"
                        onClick={debounce(async (e) => {
                          //handleNextTab();
                        }, 800)}
                      />
                    </div>
                  ) : UnitCreationDraft == false &&
                    PropertyStatus !== "REJECTED" ? (
                    <div className={styles.btnWidth}>
                      <NormalButton
                        className={"submitBtn"}
                        label={"Add More Units"}
                        id="AddMoreBtn"
                        onClick={debounce(async (e) => {
                          //handleNextTab();
                        }, 800)}
                      />
                    </div>
                  ) : UnitCreationDraft == false &&
                    PropertyStatus == "APPROVED" ? (
                    <div className={styles.btnWidth}>
                      <NormalButton
                        className={"submitBtn"}
                        label={"Submit"}
                        id="submitBtn"
                        onClick={debounce(async (e) => {
                          handleNextTab(null, false, false, true);
                        }, 800)}
                      />
                    </div>
                  ) : (
                    <></>
                  )}
                </div>
              </div>
            )}
          </>
        ) : (
          <>
            <div className="d-flex justify-content-start">
              <div
                className={`d-flex justify-content-end ${styles.btn} py-2 px-2 col`}
              >
                <div className={styles.btnWidth}>
                  <NormalButton
                    className={"draftBtn"}
                    label={"Edit Details"}
                    //id={"draftBtn"}
                    //onClick={(e) => handleSaveDraft(e)}
                    onClick={(e) => {
                      handleNextTab(urlActiveTab, true);
                    }}
                    disabled={
                      ((stayType !== "ALL" || occupancy !== "ALL") && true) ||
                      isSubmitLoading
                    }
                  />
                </div>
                <div className={styles.btnWidth}>
                  <NormalButton
                    className={"submitBtn"}
                    label={"Submit"} 
                    id="submitBtn"
                    onClick={debounce(async (e) => {
                      handleNextTab(null, false, false, true);
                    }, 800)}
                  />
                </div>
              </div>
            </div>
          </>
        )
      ) : (
        <div className={`d-flex justify-content-end ${styles.btn} py-2 px-2`}>
          <div className="d-flex align-items-center">
            <div className="px-1">
              {stayType !== "ALL" || occupancy !== "ALL" ? (
                <ToolTip label="Remove all the filters to enable actions">
                  <InfoIcon className="mt-2" />
                </ToolTip>
              ) : (
                ""
              )}
            </div>
          </div>
          <div className={styles.btnwidth}>
            <NormalButton
              className={"draftBtn"}
              label={isPreviewScreen ? "Cancel" : "Save as Draft"}
              //id={"draftBtn"}
              innerref={childSaveDraftRef}
              onClick={debounce((e) => {
                if (isPreviewScreen) {
                  handleNextTab(urlActiveTab, false, isPreviewScreen);
                  return;
                }
                if (stayType !== "ALL" || occupancy !== "ALL") {
                  Toast({
                    type: "error",
                    message: "Please remove all the filters then try..!",
                  });
                } else {
                  setSaveDraft(true);
                  setTimeout(() => {
                    handleSaveDraft(e, true);
                  }, 500);
                }
              }, 800)}
              disabled={
                ((stayType !== "ALL" || occupancy !== "ALL") && true) ||
                isSubmitLoading
              }
            />
          </div>
          <div className={styles.btnwidth}>
            <NormalButton
              disabled={
                ((stayType !== "ALL" || occupancy !== "ALL") && true) ||
                isSubmitLoading
              }
              className={"submitBtn"}
              label={isPreviewScreen ? "Save Changes" : "Save & Proceed"}
              id="submitBtn"
              //onClick={handleNextTab}
              onClick={debounce((e) => {
                if (stayType !== "ALL" || occupancy !== "ALL") {
                  Toast({
                    type: "error",
                    message: "Please remove all the filters then try..!",
                  });
                } else {
                  setSaveDraft(false);
                  handleOnSubmit(e);
                }
              }, 800)}
            />
          </div>
        </div>
      )}
      <DeleteModal
        visible={showWarningOccupancy ? true : false}
        handleClose={() => {
          setShowWarningOccupancy(null);
        }}
        handleDelete={() => {
          setOccupancy(showWarningOccupancy);
          setShowWarningOccupancy(null);
        }}
        description={`Your unsaved changes would be lost, is that okay?`}
        label="Continue"
      />

      <DeleteModal
        visible={showWarning ? true : false}
        handleClose={() => setShowWarning(null)}
        handleDelete={() => {
          setStayType(showWarning);
          setShowWarning(null);
        }}
        description={`Your unsaved changes would be lost, is that okay?`}
        label="Continue"
      />
    </div>
  );
};

export default RoomPackagesForm;
