import React, {
  forwardRef,
  useImperativeHandle,
  useEffect,
  useState,
} from "react";
import {
  useForm,
  useFieldArray,
  useFormContext,
  FormProvider,
} from "react-hook-form";
import { getRoomPackageDetails } from "service/RoomPackages";
import Table from "react-bootstrap/Table";
import TableBody from "./TableBody";
import styles from "./index.module.scss";
import DeleteModal from "components/UI/common/DeleteModal";

const RoomTypeComponent = forwardRef((props) => {
  const {
    formRef,
    activeTabName,
    roomTypeId,
    saveDraft,
    form,
    activeTab,
    roomType,
    propertyId,
    setActiveTab,
    setPackageDetails,
    packageDetails,
    isSubmitLoading,
    stayType,
    occupancy,
    handleDeletedArray,
    setIsDisabled,
    setShowFilter,
    showPreview,
    setIsDirty,
  } = props;

  const [occupancyCheck, setOccupancyCheck] = useState(false);

  const [sharingData, setSharingData] = useState([
    { value: "1", label: "1" },
    { value: "2", label: "2" },
    { value: "3", label: "3" },
    { value: "4", label: "4" },
  ]);

  const methods = useForm({
    defaultValues: {
      fieldArray: form,
    },
    mode: "onChange",
  });

  useImperativeHandle(formRef, () => {
    return {
      handleOnSubmit,
      dirtyFields: methods.dirtyFields,
      packageDetails,
      watch: methods.watch,
    };
  });

  // useEffect(() => {
  //   setShowFilter(Boolean(occupancyCheck));
  // }, [occupancyCheck])

  const { fields } = useFieldArray({
    control: methods.control,
    name: `fieldArray`,
  });

  useEffect(() => {
    setIsDirty?.(methods.formState.isDirty);
  }, [methods.formState.isDirty]);

  useEffect(() => {
    getRoomPackage();
  }, [stayType, occupancy]);

  const handleOnSubmit = (callback, e) => {
    methods.handleSubmit(callback, (err) => {
      const newError = { ...err };
      const index = newError?.fieldArray?.findIndex(
        (field) => field?.data?.length
      );
      if (index !== -1 && index !== undefined) {
        setActiveTab(index);
        setIsDisabled(false);
        const el = document.getElementsByName(
          `fieldArray.${index}.data.0.occupancy`
        );
        if (el && el[0]) {
          el[0].focus();
        }
      }
    })(e);
  };

  const getRoomPackage = async () => {
    const params = {
      stayType,
      occupancy,
    };
    const response = await getRoomPackageDetails(
      propertyId,
      roomTypeId,
      params
    );
    const data = response.data?.data || [];
    methods.reset((val) => {
      let fieldArr = val.fieldArray;
      fieldArr = fieldArr.map((field) => {
        field.data.map((item) => {
          const tempArr = data.filter(
            (o) => o.roomTypeId === field._id && o.stayType === item.key
          );
          let occupancy = false;
          item.data = tempArr.map(
            ({ __v, createdAt, updatedAt, isDraft, ...o }) => {
              if (occupancy && o.occupancy === "SHARING") {
                o.occupancy = null;
              }
              if (o.occupancy === "SHARING" && !occupancy) {
                occupancy = true;
              }
              if (o.stayType === "SHORT_STAY") {
                o.securityDepositMonths = "NA";
                o.contractType = "DAILY";
              } else {
                o.contractType = "MONTHLY";
              }
              return { ...o };
            }
          );
          return item;
        });
        return field;
      });
      return {
        fieldArray: fieldArr,
      };
    });
    setPackageDetails(data);
    //}
  };

  return (
    <FormProvider {...methods}>
      {fields.map((field, roomTypeIndex) => {
        const overAllData = methods?.watch(`fieldArray.${roomTypeIndex}.data`);
        const notFoundData = overAllData?.flatMap((a) => [...(a?.data || [])]);
        return (
          <div
            key={field.id}
            className={field._id === roomTypeId ? "" : "d-none"}
          >
            <TableForm
              roomTypeIndex={roomTypeIndex}
              saveDraft={saveDraft}
              activeTab={activeTab}
              handleDeletedArray={handleDeletedArray}
              watch={methods.watch}
              setSharingData={setSharingData}
              sharingData={sharingData}
              showPreview={showPreview}
            />
            {Boolean(
              field._id === roomTypeId && notFoundData?.length === 0
            ) && (
              <div className="d-flex justify-content-center w-100 py-3">
                No Data found
              </div>
            )}
          </div>
        );
      })}
    </FormProvider>
  );
});

RoomTypeComponent.displayName = "RoomTypeComponent";

const TableForm = (props) => {
  const {
    roomTypeIndex,
    setValue,
    saveDraft,
    activeTab,
    handleDeletedArray,
    noDataFound,
    setSharingData,
    sharingData,
    showPreview,
  } = props;

  const { control } = useFormContext();

  const { fields } = useFieldArray({
    control,
    name: `fieldArray.${roomTypeIndex}.data`,
  });

  return (
    <Table className={styles.table}>
      <thead>
        <colgroup span="1"></colgroup>
        <colgroup span="2"></colgroup>
        <tr>
          {tableheader.map((opt, i) => {
            const classs = [
              opt.data ? styles.nestHeadClass : "",
              opt.isFixed
                ? styles.fixed
                : opt.isFixed1
                ? styles.fixed2
                : opt.isFixed2
                ? styles.fixed3
                : "",
            ].join(" ");
            return (
              <th
                key={`theadOn_${i}`}
                colSpan={opt.data?.length || "1"}
                scope="colgroup"
                className={classs}
              >
                {opt.data ? opt.key : ""}
              </th>
            );
          })}
        </tr>
        <tr>
          {tableheader.map((opt, i) => {
            return !opt.data ? (
              <th
                key={opt.key}
                scope="col"
                className={
                  opt.isFixed
                    ? styles.fixed
                    : opt.isFixed1
                    ? styles.fixed2
                    : opt.isFixed2
                    ? styles.fixed3
                    : ""
                }
              >
                {opt.key}
              </th>
            ) : (
              opt.data.map((item) => (
                <th
                  key={item.key}
                  scope="col"
                  className={
                    opt.isFixed
                      ? styles.fixed
                      : opt.isFixed1
                      ? styles.fixed2
                      : opt.isFixed2
                      ? styles.fixed3
                      : ""
                  }
                >
                  {item.key}
                </th>
              ))
            );
          })}
        </tr>
      </thead>
      {fields.map((field, index) => {
        return (
          <tbody key={field.id}>
            <TableBody
              saveDraft={saveDraft}
              activeTab={activeTab}
              setValue={setValue}
              i={index}
              roomTypeIndex={roomTypeIndex}
              handleDeletedArray={handleDeletedArray}
              setSharingData={setSharingData}
              sharingData={sharingData}
              showPreview={showPreview}
            />
          </tbody>
        );
      })}
      {/* {noDataFound && <tr>
          <td colSpan={16}>
            <div className="d-flex justify-content-center w-100 py-3">
              No Data found
            </div>
          </td>
        </tr>} */}
    </Table>
  );
};

export default RoomTypeComponent;

const tableheader = [
  { key: "Stay Type", isFixed: true },
  {
    key: "Occupancy",
    isFixed1: true,
  },
  {
    key: "Sharing",
    isFixed2: true,
  },
  {
    key: "PAX",
    data: [
      { key: "Base Adult" },
      { key: "Base Child" },
      { key: "Extra Adult" },
      { key: "Extra Child" },
    ],
  },
  { key: "Contract Type" },
  {
    key: "RATES",
    data: [
      { key: "Base Rate (₹)" },
      { key: "Min threshold (₹)" },
      { key: "Max Threshold (₹)" },
      { key: "Extra Adult (₹)" },
      { key: "Extra Child (₹)" },
    ],
  },
  { key: "Rent Payment Model" },
  { key: "Security Deposit (months of rent)" },
  { key: "Token Amount (% of Rent)" },
];
